import {Injectable} from '@angular/core';
@Injectable()
export class ImageService{
    visibleimages=[];
   //for flat the images in the gallery
    getimages(){
        return this.visibleimages = Image;
    }
    //for zoom image
    getimage(id :number){
        return Image.slice(0).find(image => image.id == id)

    }
    
    }


